.page-container {
  margin: 0 auto;
  width: 800px;
  max-width: 100%;
}
.projects-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.project-box {
  width: 100%;
  margin: 20px 0;
  justify-content: center;
  display: flex;
}
.stages-container {
  display: flex;
  padding: 10px;
  justify-content: space-between;
  align-items: stretch;
}
.stage-btn {
  border: 2px dashed #ddd;
  padding: 10px;
  margin: 5px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-shrink: 1.5;
  background-color: #eee;
  cursor: pointer;
}
.stage-box {
  border: 2px dashed #ddd;
  margin: 5px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.stage-header {
	font-size: 20px;
	font-weight: bold;
	background-color: #eee;
	width: 100%;
	padding: 5px;
	text-align: center;
}

.step-btn {
	padding: 10px;
	width: 100%;
	display: flex;
	font-display: column;
	align-items: center;
	justify-content: center;
	background-color: #eee;
	cursor: pointer;
}
.step-box {
	padding: 10px;
	width: 100%;
	text-align: center;
}
